
.big-border-bottom-white {
    border-color: #fff;
}

.big-border-bottom-blue {
    border-color: #57b5d3;
}

.small-border-bottom {
    border-bottom: 0.5rem solid #57b5d3;
}

.small-border-bottom-white {
    border-bottom: 0.5rem solid #fff;
}

.big-border-top {
    border-top: 1rem solid;
}

.big-border-top-blue {
    border-top-color: #57b5d3;
}

.big-border-top-white {
    border-top-color: #fff;
}

.small-small-border-bottom {
    border-bottom-style: solid;
    border-bottom-width: 0.3rem;
}

.border-circle {
    border: 0.2rem solid;
}

.border-circle-blue {
    border-color: #57b5d3;
}

.vertical-line {
    width: 0.2rem;
}

.vertical-line-blackblue {
    background: #57b5d3;
}

.hexagon {
    clip-path: polygon(25% 0%, 75% 0%, 100% 50%, 75% 100%, 25% 100%, 0% 50%);
    width: calc(2rem + 5vw);
    height: calc(0.5rem + 5vw);
}

.hexagon-blue {
    background: #57b5d3;
}

.border-bottom-light {
    border-bottom: 1px solid;
}

.border-bottom-light-white {
    border-color: #fff;
}

.small-border-left {
    border-left: 6px solid;
}

.small-border-left-black {
    border-left-color: #000;
}

.small-md-border-right {
    border-left: 6px solid;
}

.small-md-border-right-black {
    border-left-color: #000;
}