html {
    scroll-behavior: smooth;
    scroll-padding-top: 120px;
}

.square-img {
    width: 100%;
    height: auto;
    @media (min-width:768px) {
        width: 350px;
        height: 350px;
    }

}

.img-change {
    width: 100%;
    height: auto;
    @media (min-width:768px) {
        width: 500px;
        height: 500px;
    }
}

header {
    position: fixed;
    z-index: 10;
}
.menu-mobile {
    @media (max-width:991.98px) {
        height: 100vh!important;
        position: fixed;
        right: -100vw;
        top: 0;
        z-index: 10;
        background-color: $primary-color;
        width: 75%;
        flex-flow: column;
        justify-content: start;
        transition: all 0.4s ease-out;
    }

}

.open-menu {
    right: 0;
    transition: all 0.4s ease-out;
}

a {
    &:hover {
        color: #cfcfcf !important;
    }
}