.font { 

    &-primary {
        font-size: calc(1.75rem + 2.5vw);
        line-height: 1;
        @media (min-width: 992px) {
            font-size: calc(3.75rem + 2.5vw);
        }
    }

    &-medium {
        font-size: calc(1.5rem + 1vw);
        @media (min-width: 992px) {
            font-size: calc(1.75rem + 1.75vw);
        }
    }

    &-small {
        font-size: calc(0.75rem + 0.75vw);
        @media (min-width:992px) {
            font-size: calc(0.5rem + 0.75vw);
        }
        
    }

}